.about {
  background-color: #ffffff;
  height: 90vh;
  padding: 50px 70px;
  animation: fadeIn 1.5s ease-in-out;
  font-family: "Nunito", sans-serif;
}

.aboout-contant {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  margin: 20px 2px;
}

.aboout-contant h1 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 800px;
  margin-top: -470px;
}

.aboout-contant p {
  margin-top: -230px;
  margin-left: -210px;
  padding: 40px;
  line-height: 20px;
  letter-spacing: 1px;
}

.list {
  width: 40%;
  margin: 0 auto;
  padding: 35px;
  margin-left: 60px;
  margin-top: -350px;
}

.list-skill {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
}

.list-skill p {
  font-size: 16px;
  font-weight: bold;
  color: #333;
  margin: 0;
  flex-basis: 20%;
}

.list-skill hr {
  /* flex-grow: 1; */
  border: none;
  height: 5px;
  background-color: #e38282;
  border-radius: 5px;
  margin-left: 10px;
  position: relative;
  overflow: hidden;
}

.list-skill hr::after {
  content: "";
  display: block;
  height: 100%;
  background-color: #d3d3d3;
  width: 100%;
  position: absolute;
  right: 0;
  top: 0;
  z-index: -1;
  border-radius: 5px;
}


@media (max-width: 360px) {
  .about {
    padding: 15px 10px;
    height: auto; /* Adjust height for scrolling */
    animation: fadeIn 1.5s ease-in-out;
  }

  .aboout-contant {
    flex-direction: column; /* Stack content vertically */
    align-items: flex-start;
    justify-content: center;
    padding: 10px;
    margin: 10px 0;
  }

  .aboout-contant h1 {
    width: 100%; /* Adjust width for small screens */
    margin-top: 0; /* Remove negative margin */
    font-size: 1.8rem; /* Adjust font size */
    text-align: center; /* Center align text */
  }

  .aboout-contant p {
    margin: 10px 0 0 0;
    padding: 20px 10px; 
    line-height: 1.4;
    letter-spacing: 0.02px;
    text-align: justify;
  }

  .list {
    padding: 20px;
  }

  .list-skill p {
    font-size: 14px; /* Smaller font for mobile screens */
  }

  .list-skill hr {
    width: 80%; 
  }

