@media screen and (max-width: 768px) {
  .about {
    padding: 30px 20px;
  }

  .about img {
    display: none;
  }
  .aboout-contant {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  .aboout-contant h1 {
    width: 100%;
    margin-top: 0;
    font-size: 1.8rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
  }

  .aboout-contant p {
    margin-top: 0;
    margin-left: 0;
    padding: 20px 40px;
    font-size: 1rem;
    line-height: 1.5;
  }

  .list {
    display: flex;
    flex-direction: column;
    
       
}

  .list-skill {
    display: flex;
    align-items: center;
  }

  .list-skill p {
    min-width: 130px;
    font-size: 15px;
    font-weight: 500;

  }

  .list-skill hr {
    outline: none;
    border: none;
    width: 10%;
    height: 4px;
    border-radius: 50px;
    margin-top: 7px;
  }

  .list :hover{
    transform: scale(1.05);
    transition: 0.1s;
  }

  }

  

@media screen and (max-width: 480px) {
  .about {
    padding: 20px 10px;
  }

  .aboout-contant {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  .aboout-contant h1 {
    width: 100%;
    margin-top: 0;
    font-size: 1.5rem;
  }

  .aboout-contant p {
    margin-top: 0;
    margin-left: 0;
    padding: 15px;
    font-size: 0.9rem;
    line-height: 1.4;
  }

  .list {
    margin-top: 0;
    text-align: center;
  }

  .list hr {
    width: 70%;
    margin-left: 0;
  }

  .list li {
    font-size: 1rem;
  }
}
