@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200..1000;1,200..1000&family=Playwrite+HU:wght@100..400&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Prompt:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Teko:wght@300..700&display=swap');

.footer{
        height: 80vh;
        background: #ffffff;
        padding-top: 40px;
        color: #000000;
        font-family: "Nunito", sans-serif;
    }


.footer-title{
   
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-align: center;
    
}

.footer-title h2{
    color: rgb(0, 0, 0);
    font-size: 2.1rem;
    font-weight: 500;
    font-weight: bolder;
    text-transform: capitalize;
    line-height: 15rem;
}

.footer-title p{
    width: 500px;
    margin-top: -100px;
    font-family: "Nunito", sans-serif;

}

.logo{
        margin: 50px 0px;
        gap: 20px;
}

.footers{

    background-color: black;
    height: 15vh;

}
.footer-last p{

    margin-top: 50px;
    padding: 0 20px;
    font-size: 12px;
    
}

.footer-last{
    display: flex;
    justify-content: space-between;
    font-size: 15px;
    background-color: #000000;
    color: white;
    padding: 0 1px;
}

.footer-lastone{
    display: flex;
    margin-bottom: 1px;
    margin-left: -40px;

}

.footer-lastone h5{
    padding: 28px 200px;
    font-weight: lighter;
    letter-spacing: 2px;
    color: white;
    list-style: none;
}



@media (max-width: 360px) {
    .footer {
      height: auto;
      padding-top: 20px; 
      /* font-size: 0.1rem; */
    }
  
    .footer-title {
      padding: 0 10px; 
    }
  
    .footer-title h2 {
      font-size: 30px; 
      line-height: 2.5rem; 
    }
  
    .footer-title p {
        padding: 40px;
      width: 70%; 
      font-size: 20px;
      margin-top: -10px; 
    }
  
    .logo {
      margin: 40px 0; 
      gap: 10px; 
    }
  
    .footers {
      height: auto; /* Allow the height to be dynamic */
      padding: 10px 20px; /* Add padding for smaller screens */
    }
  
    .footer-last {
      flex-direction: column; /* Stack content vertically */
      align-items: center;
      padding: 10px; /* Add padding for spacing */
      font-size: 12px; /* Adjust font size */
    }
  
    .footer-last p {
      margin-top: 20px; /* Adjust spacing */
      text-align: center; /* Center the text */
    }
  
    .footer-lastone {
      margin: 0 auto; /* Center align the last footer section */
      flex-direction: column; /* Stack items vertically */
    }
  
    .footer-lastone h5 {
      padding: 10px; /* Adjust padding */
      font-size: 0.8rem; /* Reduce font size */
      text-align: center; /* Center-align text */
    }
  }

  



/* Mobile Responsiveness */
@media (max-width: 768px) {
    .footer-title h2 {
        font-size: 2rem;
    }

    .footer-title p {
        font-size: 1rem;
        width: 90%;
    }

    .footer-last {
        flex-direction: column;
        text-align: center;
    }

    .footer-lastone {
        flex-direction: column;
        align-items: center;
    }

    .footer-last p {
        font-size: 0.9rem;
    }

    .footer-lastone a {
        font-size: 1rem;
    }
}